body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1{
  font-size: 3rem;
  text-align: center;
}
.sidebar {
  position: fixed;
  max-width: 240px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
@media only screen and (max-width: 1200px) {
  h1{
    font-size: larger;
    text-align: center;
  }
}

.footer{
  margin-top: 1rem;
  padding: 1rem;
  background-color: rgb(235, 195, 64);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

#page{
  position: relative;
  padding: 1rem;
  height: auto;
}
.footer-text{
  text-align: center;
}

#header{
  margin-bottom: 1rem;
  padding: 1rem;
  background-color: rgb(235, 195, 64);
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  
}
